* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    -webkit-tap-highlight-color: transparent;
}

/*modo claro*/
.light-mode .contenedor-notificaciones{
    background-color: #ffffff;
}
/*modo oscuro*/
.dark-mode .contenedor-notificaciones{
    background-color: #000000;
}


.contenedor-icono-notificaciones-icono-cerrar {
    width: 100%;
    height: auto;
    padding: 1rem;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

/*modo claro*/
.light-mode .contenedor-icono-notificaciones-icono-cerrar{
    background-color: #e7ff11;
}
/*modo oscuro*/
.dark-mode .contenedor-icono-notificaciones-icono-cerrar{
    background-color: #383737;
}

.contenedor-icono-notificaciones {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 1rem;
}

.contenedor-icono-notificaciones .icono-notificaciones {
    font-size: 1rem;
}

/*modo claro*/
.light-mode .contenedor-icono-notificaciones .icono-notificaciones {
    color: #000000;
}
/*modo oscuro*/
.dark-mode .contenedor-icono-notificaciones .icono-notificaciones {
    color: #ffffff;
}

.contenedor-icono-notificaciones .texto-notificaciones {
    font-size: 1rem;
    font-weight: bold;
}

/*modo claro*/
.light-mode .contenedor-icono-notificaciones .texto-notificaciones{
    color: #000000;
}
/*modo oscuro*/
.dark-mode .contenedor-icono-notificaciones .texto-notificaciones{
    color: #ffffff;
}

.contenedor-icono-notificaciones-icono-cerrar .icono-cerrar {
    font-size: 1rem;
    cursor: pointer;
}

/*modo claro*/
.light-mode .contenedor-icono-notificaciones-icono-cerrar .icono-cerrar{
    color: #000000;
}
/*modo oscuro*/
.dark-mode .contenedor-icono-notificaciones-icono-cerrar .icono-cerrar{
    color: #ffffff;
}

.contenedor-informacion-notificaciones {
    width: 100%;
    max-height: 100%;
    height: 100vh;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contenedor-informacion-notificaciones .texto-informacion-notificaciones {
    font-size: 1rem;
    font-weight: bold;
}

/*modo claro*/
.light-mode .contenedor-informacion-notificaciones .texto-informacion-notificaciones{
    color: #000000;
}
/*modo oscuro*/
.dark-mode .contenedor-informacion-notificaciones .texto-informacion-notificaciones{
    color: #ffffff;
}

/*modo Landscape para dispositivos moviles*/
@media screen and (min-width: 320px) and (max-width: 932px) and (orientation: landscape) {
    .contenedor-icono-notificaciones .icono-notificaciones {
        font-size: 1.2rem;
    }

    .contenedor-icono-notificaciones .texto-notificaciones {
        font-size: 1.2rem;
    }

    .contenedor-icono-notificaciones-icono-cerrar .icono-cerrar {
        font-size: 1.2rem;
    }

    .contenedor-informacion-notificaciones .texto-informacion-notificaciones {
        font-size: 1.2rem;
    }
}

/*Tablets y laptops pequeas*/
@media screen and (min-width: 768px) and (max-width: 1366px) {
    .contenedor-icono-notificaciones .icono-notificaciones {
        font-size: 1.2rem;
