*{
    padding: 0;
    margin: 0;
}

.container {
    display: grid;
    grid-template-columns: repeat(10, 200px);
    grid-template-rows: repeat(6, 230px);
    transform: translate(-3%, -4%);
}

/*modo claro*/
.light-mode .shape polygon:nth-child(1){
    stroke: hsl(320, 100%, 70%);
}

.light-mode .shape polygon:nth-child(2){
    stroke: hsl(240, 100%, 70%);
}

.light-mode .shape polygon:nth-child(3){
    stroke: hsl(160, 100%, 70%);
}

.light-mode .shape polygon:nth-child(4){
    stroke: hsl(80, 100%, 70%);
}


/*modo oscuro*/
.dark-mode .shape polygon:nth-child(1){
    stroke: hsl(0, 0%, 80%);
}

.dark-mode .shape polygon:nth-child(2){
    stroke: hsl(0, 0%, 60%);
}

.dark-mode .shape polygon:nth-child(3){
    stroke: hsl(0, 0%, 40%);
}

.dark-mode .shape polygon:nth-child(4){
    stroke: hsl(0, 0%, 20%);
}



.shape {
    width: 200px;
    height: 230px;
    -webkit-clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0 75%, 0 25%);
    clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0 75%, 0 25%);
}

